@import '../../node_modules/swiper/swiper-bundle.css';
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "NoirPro";
  src: url("../fonts/NoirPro-Regular.woff2") format("woff2"), url("../fonts/NoirPro-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NoirPro";
  src: url("../fonts/NoirPro-Bold.woff2") format("woff2"), url("../fonts/NoirPro-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  font-family: "NoirPro", sans-serif;
  font-size: 16px;
  font-weight: 400;
  min-width: 320px;
  overflow-x: hidden;
  color: #311746;
}

.container,
.container-lg {
  margin: 0 auto;
  padding: 0 15px;
}

.container-lg {
  max-width: 1360px;
}

.container {
  max-width: 1150px;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}

.title {
  font-family: "NoirPro", sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.2em;
}

.color-primary {
  color: #E73348;
}

.text-uppercase {
  text-transform: uppercase;
}

.btn-primary,
.btn-secondary {
  color: #fff;
  border-radius: 4px;
  padding: 23px 44px;
  font-weight: 700;
  transition: background-color, 0.3s ease;
}
.btn-primary:hover,
.btn-secondary:hover {
  background-color: transparent;
  transition: background-color, 0.3s ease;
}

.btn-primary {
  background-color: #E73348;
  border: 1px solid #E73348;
}
.btn-primary:hover {
  color: #E73348;
}

.btn-secondary {
  border: 1px solid #fff;
  background-color: #fff;
  color: #E73348;
  padding: 23px 32.5px;
}
.btn-secondary:hover {
  color: #fff;
}

.popup-login {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease;
}
.popup-login .login__inner {
  height: 100%;
  width: 100%;
}
.popup-login .login__wrapper {
  height: 100%;
  justify-content: center;
}

.popup-close {
  width: 35px;
  position: absolute;
  top: 45px;
  right: 45px;
}
.popup-close__item {
  width: 100%;
  height: 2px;
  background-color: #311746;
}

.popup-close__item:first-child {
  margin-bottom: -1px;
  transform: rotate(45deg);
}

.popup-close__item:last-child {
  transform: rotate(-45deg);
}

.popup-login.open {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease;
}

.mabile-status {
  display: none;
  padding-top: 14px;
}

.mabile-status__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.mobile-status__time {
  font-size: 15px;
  font-weight: 600;
}

.header {
  padding: 53px 0 20px 0;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.header__login {
  display: flex;
  align-items: center;
}

.user {
  display: flex;
  align-items: center;
  margin-right: 70px;
}

.user__content {
  display: flex;
  flex-direction: column;
  color: #E73348;
}

.user__greeting {
  font-size: 14px;
}

.user__name {
  font-weight: 700;
  line-height: 1.3em;
}

.user__img {
  margin-right: 10px;
}

.dropdown {
  width: 30px;
  height: 30px;
  border: 1px dashed #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
}

.autors {
  padding-top: 57px;
  padding-bottom: 190px;
}

.autors__inner {
  position: relative;
  overflow-x: hidden;
}

.autors__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 40px;
}

.search-form {
  max-width: 260px;
  width: 100%;
  display: flex;
}

.search-form__input,
.search-form__btn {
  border: none;
  background-color: #FFF7F7;
  padding: 0;
  margin: 0;
}

.search-form__input {
  flex-grow: 1;
  padding: 16px 10px 16px 16px;
  border-radius: 4px 0px 0px 4px;
  outline: none;
}
.search-form__input::placeholder {
  color: #837490;
  font-family: "NoirPro", sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.search-form__btn {
  padding: 0 10px;
  border-radius: 0px 4px 4px 0px;
}

.autors-slider {
  max-width: 1150px;
  padding: 0 15px;
  margin: 0 auto;
  overflow: hidden;
}

.swiper-slide {
  max-width: 256px;
}

.autors-slider__btn {
  position: absolute;
  left: 0;
  right: 0;
  top: 60%;
  z-index: 2;
}

.swiper-button-next,
.swiper-button-prev {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #E73348;
  position: absolute;
  z-index: 2;
}
.swiper-button-next::after,
.swiper-button-prev::after {
  content: url("../images/svg/arrow-slider.svg");
  position: absolute;
  font-size: 0;
}

.swiper-button-next {
  right: 45px;
}

.swiper-button-prev {
  left: 45px;
}
.swiper-button-prev::after {
  transform: rotate(180deg);
}

.card {
  height: 430px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  position: relative;
  z-index: 1;
  color: #fff;
  padding: 20px;
  display: flex;
  align-items: flex-end;
  transition: all 0.5s ease;
}
.card::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(0deg, #171717 20.12%, rgba(23, 23, 23, 0) 100%);
  z-index: -1;
}

.card:hover::after {
  background-image: linear-gradient(0deg, #171717 63.95%, rgba(23, 23, 23, 0) 100%);
}

.card_bg1 {
  background-image: url("../images/cards/cadr_1.jpg");
}

.card_bg2 {
  background-image: url("../images/cards/card_2.jpg");
}

.card_bg3 {
  background-image: url("../images/cards/card_3.jpg");
}

.card_bg4 {
  background-image: url("../images/cards/card_4.jpg");
}

.card_bg5 {
  background-image: url("../images/cards/card_5.jpg");
}

.card__title {
  font-size: 30px;
  line-height: 1.2em;
  margin-bottom: 10px;
  max-width: 210px;
  text-transform: capitalize;
  transition: all 0.3s ease-out;
}

.card__text {
  font-size: 18px;
  line-height: 1.3em;
  margin-bottom: 20px;
  overflow-y: hidden;
}

.card__text-hover {
  margin-bottom: -110px;
  transition: all 0.3s ease-out;
}

.card:hover .card__title {
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-out;
}
.card:hover .card__text-hover {
  margin-bottom: 0px;
  transition: all 0.3s ease-out;
}

.card__btn-padding {
  padding: 23px 38px;
}

.card.card__create-community::after {
  background-image: none;
  background-color: #E73348;
}

.login__inner {
  padding: 0 20px;
}

.login__wrapper {
  max-width: 352px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  text-align: center;
}

.login__title {
  font-size: 30px;
  line-height: 1.36em;
  margin-bottom: 40px;
}

.form-login {
  margin-bottom: 40px;
}

.form-control__input {
  width: 100%;
  border: none;
  padding: 23px 10px 23px 16px;
  filter: drop-shadow(0px 3px 10px rgba(88, 19, 27, 0.1));
  border-radius: 4px;
  margin-bottom: 20px;
}
.form-control__input::placeholder {
  font-family: "NoirPro", sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #837490;
}

.form-login__btn {
  width: 100%;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  padding: 22px 0;
}

.fragot-password {
  font-size: 18px;
  margin-bottom: 36px;
  text-align: left;
}
.fragot-password span {
  display: inline-block;
  margin-bottom: 10px;
}

.fragot-password__link {
  text-decoration: underline;
}

.social-login {
  margin-bottom: 30px;
}

.social-login__title {
  font-size: 24px;
  margin-bottom: 20px;
}

.social__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn-social {
  border: 1px solid;
  border-radius: 4px;
  position: relative;
  padding: 21px 41px;
  font-weight: 700;
}
.btn-social::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.btn-social--facebook {
  border-color: #4968AD;
  color: #4968AD;
}
.btn-social--facebook::after {
  background-image: url("../images/svg/facebook.svg");
}

.btn-social--google {
  border-color: #E73348;
  color: #E73348;
  padding: 21px 51px;
}
.btn-social--google::after {
  background-image: url("../images/svg/google.svg");
}

.register {
  font-size: 18px;
}
.register__link {
  text-decoration: underline;
}
.register span {
  display: inline-block;
  margin-bottom: 10px;
}

.footer {
  padding: 30px 0;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.footer__privacy {
  display: flex;
  align-items: center;
}
.footer__privacy-item {
  margin-right: 12px;
}

.footer__copy {
  font-size: 14px;
  color: rgba(49, 23, 70, 0.5);
}

.footer__inner--login {
  justify-content: flex-end;
}

@media only screen and (min-width: 1140px) {
  .autors-slider {
    overflow: initial;
  }

  .swiper-slide {
    opacity: 0.5;
  }

  .swiper-slide.swiper-slide-visible {
    opacity: 1;
  }
}
@media only screen and (max-width: 525px) {
  .user {
    margin-right: 0;
  }

  .dropdown {
    display: none;
  }

  .autors__heading {
    flex-direction: column;
    align-items: flex-start;
  }

  .search-form {
    max-width: 100%;
  }

  .title {
    max-width: 220px;
    font-size: 36px;
    margin-bottom: 20px;
  }

  .swiper-button-next,
.swiper-button-prev {
    width: 64px;
    height: 64px;
    position: static;
  }

  .autors-slider__btn {
    max-width: 138px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    top: 45px;
    left: auto;
    right: 20px;
  }
}
@media only screen and (max-width: 400px) {
  .mabile-status {
    display: block;
  }

  .header {
    padding-top: 43px;
  }

  .user__content {
    display: none;
  }

  .title {
    max-width: 200px;
  }

  .swiper-button-next,
.swiper-button-prev {
    width: 45px;
    height: 45px;
  }

  .autors-slider__btn {
    max-width: 100px;
  }

  .footer__inner {
    flex-direction: column;
  }

  .footer__privacy {
    margin-bottom: 20px;
  }

  .social__links {
    flex-direction: column;
  }

  .social__links-item:first-child {
    margin-bottom: 20px;
  }
}